<template>
  <div class="bono-view">
        <div class="report-row" style="display: flex; ">
            <a class="close-btn" @click="$emit('cancel')">go back</a>
            <span style=""></span>
        </div>
        <h3>A√±adir Bono a lista</h3>
        <form action="#" v-on:submit.prevent @submit="addbono">
          <div>
            <div style="padding: 0 10%">
                <div class="more-info">
                    <span>nombre</span>
                    <input name="bname" type="text" required>
                <!--span class="desc-info">nombre referncial atribuido al bono</span-->
                </div>
                <div class="more-info">
                    <span>RUC</span>
                    <input name="bruc" type="text" value="" pattern="^([0-9]{11})?$">
                <!--span class="desc-info">codigo identificador de la empresa que brinda el bono (opcinal)</span-->
                </div>
            </div>
            <div>
                <div class="report-row" style="display: flex; ">
                    <span style="">tipo de bono:</span>
                    <span style="">{{tipo_bono(meta_bono)}}</span>
                </div>
                <div class="report-row">
                    <span>duraci√≥n del periodo:</span>
                    <span>{{meta_bono.periodo}} a√±os</span>
                </div>
                <div class="report-row">
                    <span>pago por cup√≥n:</span>
                    <span>{{meta_bono.cupon}}</span>
                </div>
                <div class="report-row">
                    <span>frecuencia entre pagos:</span>
                    <span>{{meta_bono.frecuencia}} d√≠as</span>
                </div>
                <div class="report-row">
                    <span>numero de pagos recibidos:</span>
                    <span>{{meta_bono.cxp}} cupones</span>
                </div>
            </div>
            <div style="border: solid 1px; padding: .5rem 1rem">
                <InputIndicative :namep="'vnom'" :title="'valor nominal'" :descr="'Valor nominal del bono'"      :value="meta_bono.valor_nominal"   :disabled="true"/>
                <InputIndicative :namep="'pbac'" :title="'precio del cup√≥n'" :descr="'Valor comercial del bono'" :value="meta_bono.precio_actual"   :disabled="true"/>
                <InputIndicative :namep="'pbac'" :title="'retorno del cup√≥n'" :descr="'Suma de las rentas devueltas del bono'" :value="meta_bono.valor_actual"    :disabled="true"/>
                <InputIndicative :namep="'avna'" :title="'VNA'" :descr="'Valor nominal del bono'"                :value="meta_bono.vna"             :disabled="true"/>
            </div>
            <button type="submit">Guardar</button>
          </div>
        </form>
  </div>
</template>

<script>
    import InputIndicative from './InputIndicative.vue';
    
    export default {
        data: () =>({
            bono:{
                periodo : 5,
                coupon : 10,
                cxp : 10,
                frecuencia : 180
            }
        }),
        props:{
            meta_bono : Object
        },
        computed:{
        },
        watch:{
        },
        components:{
            InputIndicative
        },
        methods:{
            tipo_bono(bono){
                console.log(bono);
                switch (Math.sign(bono.valor_nominal - bono.precio_actual)) {
                    case 0:
                    return 'bono emitido a la par';
                    case 1:
                    return 'bono emitido a descuento';
                    case -1:
                    return 'bono con prima';
                }
            },
            addbono(e){
                const formData = new FormData(e.target);
                var vals = {};

                for (var pair of formData.entries())
                    vals[pair[0]] = pair[1];
                
                console.log(vals);

                this.meta_bono.nombre = vals.bname;
                this.meta_bono.ruc = "" + vals.bruc;
                this.$emit('save', this.meta_bono);
            }
        },
        mounted (){

        }
    }

</script>

<style scoped>
.desc-info{
    display: none;
    position: absolute;
    left: 0;
    top: 100%;
}
.more-info > input:focus + .desc-info{
    display: initial;
}

.more-info{
    position: relative;
    width: 100%;
    margin: auto;
    display: flex;
    margin-bottom: 4px;

    border-bottom: 1px solid;
}
.more-info > span:first-child{
    text-align: left;
    display: block;
    margin-top: auto;

    font-size: 12px;
    font-weight: 600;
}
.more-info > input{
    text-align: right;
    flex: 1;
    margin-left: .5rem;
    display: block;
    
    font-size: 15px;

    background: none;
    border: none;
    outline: none;
}

.close-btn{
    color: #000;
    opacity: .5;
    font-weight: 600;
    cursor: pointer;
}
.close-btn:hover{
    opacity: .75;
}
.report-row{
    display: flex;
}
.report-row > *{
    display: inline-block;  font-size: 11px;  margin-top: auto;
}
.report-row >*:last-child{
    margin-left: auto; margin-top: 0; font-size: 15px;
}

.bono-view{
    min-width: 360px;
}
form > div > div{
    margin-bottom: .5rem;
}


</style>