<template>
  <div class="input-indicative">
    <label :for="this._uid">{{title}}</label>
    <div class="input-info"><div><p v-html="descr"></p></div></div>
    <input class="input-numb" 
        :readonly="readonly" 
        :disabled="disabled" 
        :value="value" 
        :name="namep"
        v-once
        type="number" 
        step="any"
        :id="this._uid"
        @input="$emit('input', $event.target.value)">
    <div v-if="!(error === undefined)" class="error-message">
        <span>{{error}}</span>
    </div>
  </div>
</template>

<script>
export default {
    name:'InputIndicative',
    data:()=>({
        id : null
    }),
    props:{
        title : String,
        descr : String,
        namep : String,
        error: String,
        mods: {
            type:Array,
            default: ()=>[]
        },
        readonly: {
            type:Boolean,
            default :false
        },
        disabled:{
            type:Boolean,
            default :false
        },
        value :{
            type : Number,
            default :0
        }
    },
    mounted(){
        let input = document.getElementById(this._uid);
        this.mods.forEach(mod => {
            input.addEventListener(mod.trigger, mod.action);
        });
    }
}
</script>

<style scoped>
    .input-indicative{
        margin-bottom: .75rem;
    }
    .error-message{
        color: #800;
        text-align: right;
        line-height: 1;
        font-style: italic;
        font-size: 75%;

        height: 0;
        overflow: visible;
    }
    div{
        display: flex;
        flex-direction: column;
    }

    .input-numb{
        outline: none;
        text-align: right;
    }
    label{
        text-align: left;
        font-size: 80%;
        width: auto;
        margin-right: auto;

        transition: color .3s;
        text-transform: capitalize;
    }
    label:hover{
        color: #48f;
    }
    .input-info{
        position: relative;
        transform: translateY(32px);
        width: 80%;
        margin:auto;

        font-size: 75%;
        line-height: 1.2;

        text-align: left;

        opacity: 0;

        height: 0px;

        display:block;

        pointer-events:none;
        transition: opacity .3s;
    }
    label:hover + .input-info, .calculator .input-info:hover {
        opacity: 1;
    }
    .input-info > div{
        background: #bdf;
        border: solid 1px #444;
        border-radius: 2px;


        padding: 8px;

        position: relative;
        transform: translateY(-50%);
    }

    .input-info p{
        margin: 0;
    }

    .input-numb::-webkit-outer-spin-button,
    .input-numb::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }
</style>