<template>
  <div class="calculator" >
      <form action="#" v-on:submit.prevent @submit="calculate">
          <div>
                <InputIndicative namep="vnom" title="valor nominal" :error="ope_error.vnom"
                    descr="Valor nominal del bono.<br>Valores mayores a 0."/>
                <InputIndicative :error="ope_error.tcup" namep="tcup" title="tasa del cup√≥n(%)" 
                    descr="Tasa efectiva anual aplicada al cup√≥n.<br>Valores (de 1 hasta 100%)." />
                <InputIndicative :error="ope_error.frec" namep="frec" title="frecuencia de capitalizaci√≥n(d)" 
                    descr="N√∫mero de d√≠as en un perido regular de pago del cup√≥n.<br>Valores m√∫ltiplos de 30 (de 30 hasta 360 d√≠as)."
                    :mods="[
                        {
                            trigger : 'focusout',
                            action : (event) => {
                                let val = parseFloat(event.target.value);
                                val = Math.round(val / 30) * 30;
                                val = Math.max(val, 30);
                                event.target.value = val;
                            }
                        }
                    ]"
                     />
                <InputIndicative :error="ope_error.peri" namep="peri" title="periodo(a)" 
                    descr="N√∫mero de a√±os de pago.<br>Valores enteros (de 1 - 20 a√±os)." />
                <InputIndicative :error="ope_error.tmar" namep="tmar" title="tasa del mercado(%)" 
                    descr="Tasa efectiva anual de mercado.<br>Valores (de 1 hasta 100%)." />
                <InputIndicative :error="ope_error.prim" namep="prim" title="prima del cup√≥n(%)"
                    descr="Prima o porcentaje del valor nominal.<br>Valores (de 0 hasta 50%)." />
                <button type="submit">Calcular</button>
          </div>
        </form>
        <form v-on:submit.prevent @submit="analize">
            <div v-if="bono.evo > 0">
                <InputIndicative namep="vnac" title="valor del cup√≥n" descr="Valor nominal del bono"
                    :value="meta_bono.valor_actual" :readonly="true"
                    :key="meta_bono.valor_actual"
                    />
                <InputIndicative 
                    :error="ope_error.pbac" namep="pbac" title="precio del cup√≥n" descr="Valor comercial del bono" />

                <div style="display: block">
                    <label style="font-size: 90%"><input v-model="adv" type="checkbox" name="adv">datos adicionales</label>
                </div>

                <InputIndicative 
                    :error="ope_error.flot" v-if="adv" namep="flot" title="flotaci√≥n(%)" descr="Porcentaje por gastos de flotaci√≥n del Bono.<br>Valores (de 0 hasta 10%)"/>
                <InputIndicative 
                    :error="ope_error.cava" v-if="adv" namep="cava" title="cavali(%)" descr="Porcentaje por caval√≠ del Bono<br>Valores (de 0 hasta 10%)"/>
                
                <InputIndicative namep="avna" title="VNA" descr="Valor nominal del bono" 
                    :value="meta_bono.vna" :disabled="true"
                    :key="meta_bono.vna"
                    />
                <InputIndicative namep="bc" title="B/C" descr="Indicador porcentual Beneficio/Costo." 
                    :value="(meta_bono.valor_actual / meta_bono.precio_actual).toFixed(2)" :disabled="true"
                    :key="meta_bono.vna"
                    />
                
                <button type="submit">Analizar</button>
                <button v-show="saving && bono.evo > 1" @click="save" type="button">Guardar</button>
            </div>
        </form>
        <form v-on:submit.prevent @submit="findTIR">
            <div v-if="bono.evo > 1" class="valueButton">
                <button type="submit">hallar la TIR</button>
                <span>{{meta_bono.TIR}}</span>
            </div>
            <div class="valueButtonTag" style="height: 0; overflow: show; position: relative;">
                <p>Calcular el TIR es una operaci√≥n pesada que puede tardar unos minutos, tiene a su dispocici√≥n otros 2 indicadores de rentabilidad (VNA y B/C), esta seguro de que desea calcular el TIR?
                </p>
            </div>
        </form>

  </div>
</template>

<script>
    import * as finance from '../assets/finance';
    import InputIndicative from './InputIndicative.vue';
    
    export default {
        data: () =>({
            tries : 0,
            adv : true,
            bono : {
                valor_actual: 0,
                precio_actual: 0,
                pagos: [],
                vna : 0,
                nombre: "",
                evo: 0,
            },
            meta_bono : {
            },
            ope_error :{
            },
            validation:{
                allow_zero: ["prim","flot","cava"],
                max_limit: {
                    tcup : 50,
                    frec : 360,
                    peri : 20,
                    tmar : 50,
                    prim : 50,
                    flot : 10,
                    cava : 10
                }
            }
        }),
        props:{
            saving : Boolean
        },
        components:{
            InputIndicative
        },
        methods:{
            save(){this.$emit('save', this.meta_bono);},
            param_validate(params, formData){
                let campos_null = [];
                let campos_zero = [];
                let campos_negativo = [];
                
                for (var pair of formData.entries()) {
                    let aux = pair[1];
                    let aux_float = parseFloat(aux);

                    if(aux === "")      campos_null.push(pair[0]); else
                    if(aux_float < 0)   campos_negativo.push(pair[0]); else
                    if(aux_float === 0)  campos_zero.push(pair[0]);

                    params[pair[0]] = aux_float;
                    }
                this.ope_error = {};

                campos_zero = campos_zero.filter(e => !this.validation.allow_zero.includes(e));

                campos_null.forEach(campo => this.ope_error[campo]      = "el campo no puede ser un valor nulo");
                campos_zero.forEach(campo => this.ope_error[campo]      = "el campo no puede tener un valor 0");
                campos_negativo.forEach(campo => this.ope_error[campo]  = "el campo no puede tener un valor negativo");

                if(campos_null.length + campos_zero.length + campos_negativo.length > 0) return true; else{
                    for (const param in params)
                        if (this.validation.max_limit.hasOwnProperty(param) && params[param] > this.validation.max_limit[param])    this.ope_error[param] = `el valor excede el maximo permitido que es ${this.validation.max_limit[param]}`;
                }

                return (Object.keys(this.ope_error).length > 0);
            },
            calculate(e){
                const formData = new FormData(e.target);
                let params = {};

                if(this.param_validate(params, formData)) return;
                console.log(params);

                var temp_bono = finance.default.BonoCalculadora(
                    params.vnom,
                    params.tcup,
                    params.frec,
                    params.peri,
                    params.tmar,
                    params.prim);

                this.meta_bono = {};

                this.bono.valor_actual = temp_bono.VNA;
                this.bono.pagos = temp_bono.pagos;

                this.meta_bono.valor_nominal    = params.vnom;
                this.meta_bono.valor_actual     = temp_bono.VNA;
                this.meta_bono.frecuencia       = params.frec;
                this.meta_bono.periodo          = params.peri;
                this.meta_bono.cxp              = params.peri * 360 / params.frec;

                this.meta_bono.prima            = params.prim / 100;
                this.meta_bono.cupon            = temp_bono.cupon;

                this.bono.evo = 1;
            },
            analize(e){
                const formData = new FormData(e.target);

                var params = {};
                if(this.param_validate(params, formData)) return;
                console.log(params);
                
                var temp_bono = finance.default.BonoAnalisis({
                    VNA : this.bono.valor_actual,
                    pagos : this.bono.pagos
                    }, 
                    params.pbac,
                    params.flot,
                    params.cava
                );
                
                this.meta_bono.TIR = null;

                this.bono.vna = temp_bono.VNA;

                this.meta_bono.precio_actual    = temp_bono.precio_de_venta;
                this.meta_bono.vna              = temp_bono.VNA;

                this.bono.evo = 2;
            },
            findTIR(e){
                this.bono.evo = 3;
                this.meta_bono.TIR = finance.default.BonoTIR(this.meta_bono);
            }
        },
        mounted (){

        }
    }

</script>

<style scoped>
    .error-modal{
        padding: 1rem;
    }
    .calculator{
        background-color: #8af;
        padding: 1rem;

        max-width: 360px;
        margin: 0 auto;
        margin-bottom: 1rem;

        border:none;
        box-shadow: 0 4px 16px -8px #000d;
    }
    form > div{
        display: flex;
        flex-direction: column;
    }
    form > div > div{
        display: flex;
        flex-direction: column;
    }

    button{
        width: auto;
        padding: 1rem;
        border: none;
        border-radius: .2rem;
        margin: 0 auto;
        margin-bottom: 10px;

        font-weight: 600;

        background-color: #048;
        color: #fff;
        transition: background-color .3s, color .3s;
    }
    button:hover{
        color: #000;
        background-color: #dfe;
    }
    button:hover:active{
        box-shadow: #0008 inset 0 0 16px 1px, #0008 inset 0 0 2px 0px;
    }

    .valueButton{
        display: flex;    
        flex-direction: row;
        width: 80%;
        margin: auto;
    }
    .valueButton > *{
        margin : 0;
        padding: .25rem .75rem;
        font-size: 75%;
    }
    .valueButton > button{
        background-color: #077;
        color: white;
    }
    .valueButton > button:hover{
        background-color: #088;
    }
    .valueButton > span{
        flex : 1;
        flex-direction: row;
        background-color: #fff;
        border: solid 1px #666;
        border-radius: 2px;
    }
    .valueButton > span:last-child{
        text-align: right;
        margin-left: .5rem;
    }
    div.valueButtonTag{
        opacity: 0;
    }
    .valueButton:hover + div.valueButtonTag{
        opacity: 1;
    }
    .valueButtonTag > p{
        text-align: justify;
        font-size: 75%;
        line-height: 1.2;
        background-color: #fff;
        padding: .25rem;
        border: solid 1px #666;
        border-radius: 2px;

        position: relative;
        top: -64px;
        transform: translateY(-100%);
        pointer-events: none;
    }
</style>