<template>
  <div class="calculator">
    <Calculator 
      :saving="$store.state.current_user.logged" 
      @save="saveBonoDialog($event)"
      />
    
    <div v-show="saving" style="background-color: #0004; position: fixed; left:0; top: 0; width: 100%; height: 100%"></div>
    <div class="popup" v-show="saving">
      <AddBono :meta_bono="bono" v-if="saving" @save="saveBono" @cancel="saving=false"/>
    </div>
  </div>
</template>

<script>
import AddBono from '../components/AddBono.vue';
import Calculator from '../components/Calculator.vue'

export default {
  data:()=>({
    saving: false,
    bono: {}
  }),
  components:{
    Calculator,
    AddBono
  },
  methods:{
    saveBonoDialog(bono){
      this.bono = bono;
      this.saving = true;
    },
    saveBono(bono){
      this.$store.dispatch('pre_load');
      this.$store.state.aux_bonos.push(bono);
      this.$store.dispatch('post_bonos');

      this.saving=false;

      this.bono = {}
    }
  }
}
</script>

